<template>
  <div class="sidebar-wrapper" :class="{mobileShow}">
    <div class="opacity" @click="mobileShow = false" />
    <div class="sidebar">
      <el-menu router class="second">
        <div class="nav-container-dark">
          <el-select v-model="value" placeholder="Select" popper-class="campaign-select"	 class="campaign-select">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
          <div class="campaign">
            <span class="campaign-name">
              no name
            </span>
            <span>
              <ii-copy-alt/>
              <ii-delete/>
            </span>
          </div>
          <div class="buttons">
            <el-button type="primary">
              New Page
            </el-button>
            <el-button type="info">
              Import Template
            </el-button>
          </div>
        </div>
      </el-menu>
      <el-menu router :class="{collapsed: isCollapsed}" class="first">
        <div class="nav-container">
          <Modal v-model="dialogVisible"/>
          <h3 class="nav-section">Menu</h3>
          <el-menu-item @click="dialogVisible = true">
            <ii-rocket/>
            <span>Create</span>
          </el-menu-item>
          <el-menu-item index="/campaigns">
            <ii-folder/>
            <span>Campaigns</span>
          </el-menu-item>
          <el-menu-item index="#">
            <ii-settings/>
            <span>Settings</span>
          </el-menu-item>
          <el-menu-item index="#">
            <ii-contest/>
            <span>Contests</span>
          </el-menu-item>
          <h3 class="nav-section">Support</h3>
          <el-menu-item index="#">
            <ii-tutorial/>
            <span>Tutorials</span>
          </el-menu-item>
          <el-menu-item index="#">
            <ii-support/>
            <span>Support</span>
          </el-menu-item>
        </div>
        <div class="nav-container-dark">
          <h3 class="nav-section">Upgrades</h3>
          <el-menu-item index="#">
            <ii-windows/>
            <span>All Access Pass</span>
          </el-menu-item>
          <el-menu-item index="#">
            <ii-link/>
            <span>20 Extra Domains</span>
          </el-menu-item>
          <el-menu-item index="#">
            <ii-gift/>
            <span>Bonuses</span>
          </el-menu-item>
        </div>
      </el-menu>
    </div>
  </div>
</template>

<script>
import Modal from "@/components/Modal";

export default {
  components: {
    Modal,
  },
  data: () => ({
    dialogVisible: false,
    isCollapsed: false,
    mobileShow: false,
    options: [{
      value: 'Option1',
      label: 'Option1'
    }, {
      value: 'Option2',
      label: 'Option2'
    }, {
      value: 'Option3',
      label: 'Option3'
    }, {
      value: 'Option4',
      label: 'Option4'
    }, {
      value: 'Option5',
      label: 'Option5'
    }],
    value: ''
  }),
  watch: {
    '$route': {
      immediate: true,
      deep: true,
      handler(val) {
        this.isCollapsed = !!val.meta.sidebar;
      }
    }
  },
  methods: {
    openSidebar() {
      this.mobileShow = !this.mobileShow;
    }
  },
  created() {
    this.$bus.$on('open-sidebar', this.openSidebar);
  }
}
</script>

<style lang="scss">
.campaign-select {
  .el-input {
    border-radius: 0px !important;

    &__inner {
      background-color: $clr-nav-bg;
      height: 70px;
      border: none !important;
      border-radius: 0px !important;
    }
  }
}
</style>

<style lang="scss" scoped>
.opacity {
  position: absolute;
  top: 53px;
  left: 0;
  width: 100vw;
  height: calc(100% - 53px);
  background-color: black;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.4s;
}

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  &-wrapper {
    width: 265px;
    display: flex;
  }

  .el-button {
    width: 100%;
    margin-left: 0;
  }
}

.campaign {
  height: 57px;
  background-color: $clr-primary;
  padding: 0 20px;
  display: flex;

  align-items: center;
  justify-content: space-between;

  &-name {
    color: white;
  }

  svg {
    color: $clr-txt-nav;
  }
}

.buttons {
  padding: 0 12px;
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
  }
}

.el-select {
  width: 100%;

  .el-input {
    &__inner {
      height: 70px;
    }
  }
}

.first {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 265px;
  height: 100%;
  transition: all 1s ease;
  overflow: hidden;
}

.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0px;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover {
      color: white;
    }

    &:active, &.is-active {
      color: $clr-primary;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  svg {
    margin-right: 10px;
    height: 20px;
  }

  .nav-container {
    background-color: $clr-nav-bg;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: $clr-nav-bg-dark;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.second {
  padding-left: 36px;

  .nav-container, .nav-container-dark {
    background: $clr-nav-bg;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    transform: translate(-30px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: $clr-nav-bg-dark;
  }

  .el-menu-item {
    padding: 0 !important;

    span {
      opacity: 0;
    }
  }
}

@media (max-width: $media-mobile) {
  .sidebar {
    left: -265px;
    width: 0px;
  }

  .sidebar-wrapper {
    width: 0px;
  }

  .mobileShow {
    .sidebar {
      position: absolute;
      top: 53px;
      left: 0;
      width: 265px;
      height: calc(100% - 53px);
    }

    .opacity {
      opacity: 0.8;
      pointer-events: auto;
    }
  }
}

.nav-section {
  margin-top: 52px;
  margin-left: 40px;
  font-size: 14px;
  text-transform: uppercase;
}
</style>
