<template>
  <div class="campaigns">
    <el-row type="flex" :gutter="50" style="flex-wrap: wrap; margin-bottom: 25px;">
      <el-col :span="24" class="filters">
        <Team/>
        <div class="filters-mobile">
          <el-select placeholder="Filters" v-model="selectedFilter">
            <el-option v-for="f in filters" :key="f.title" :value="f.title">
              {{ f.title }}
            </el-option>
          </el-select>
        </div>
        <div class="filters__inner">
          <div v-for="f in filters" :key="f.title" class="filter">
            <component :is="f.icon"/>
            {{ f.title }}
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" :gutter="50" style="flex-wrap: wrap;" class="cards">
      <el-col v-for="(_, index) in 20" :key="index" :xs="24" :sm="12" :md="8" :lg="8" :xl="4" >
        <div class="card">
          <img src="~@/assets/dummy.png">
          <div class="cover">
            <router-link to="/preview" id="preview">
              <ii-preview/>
            </router-link>
            <router-link to="/select" id="select">
              <ii-select/>
            </router-link>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Team from "@/components/Team";
import IconMail from '@/components/icons/Mail';
import IconCart from '@/components/icons/Cart';
import IconLock from '@/components/icons/Lock';
import IconPresentation from '@/components/icons/Presentation';
import IconChart from '@/components/icons/Chart';
import IconProfile from '@/components/icons/Profile';
import IconDownload from '@/components/icons/Download';
import IconCup from '@/components/icons/Cup';
import IconGeo from '@/components/icons/Geo';
import IconBlank from '@/components/icons/Blank';

export default {
  name: "Campaigns",
  components: {Team},
  data: () => ({
    filters: [
      { icon: IconMail, title: 'Optin', },
      { icon: IconCart, title: 'Landing', },
      { icon: IconLock, title: 'Membership', },
      { icon: IconPresentation, title: 'Webinar', },
      { icon: IconChart, title: 'Upsell', },
      { icon: IconProfile, title: 'Affiliate', },
      { icon: IconDownload, title: 'Download', },
      { icon: IconCup, title: 'Pre Launch', },
      { icon: IconGeo, title: 'My Templates', },
      { icon: IconBlank, title: 'Blank', },
    ],
    selectedFilter: '',
  }),
}
</script>

<style lang="scss">
.filters-mobile {
  .el-input {
    border-radius: 10px !important;
    font-size: 12px;

    i {
      line-height: 25px;
    }

    &__inner {
      background-color: transparent;
      width: 130px;
      height: 25px;
      border: 1px solid $clr-grey !important;
      border-radius: 10px !important;
    }
  }
}
</style>

<style lang="scss" scoped>

.campaigns .el-row {
  padding: 0 25px;
}

.card {
  //width: 100%;
  //height: 100%;
  display: flex;
  background-color: $clr-bg-light;
  min-height: 238px;
  width: 100%;
  max-width: 273px;
  max-height: 303px;
  margin: auto;
  margin-bottom: 20px;
  position: relative;

  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }

  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0;
    transition: all 0.4s ease;

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      opacity: 0.8;
    }

    #preview {
      color: $clr-primary;
      margin-right: 36px;

      &:hover {
        transform: scale(1.06);
      }
    }

    #select {
      color: $clr-green;

      &:hover {
        transform: scale(1.06);
      }
    }
  }
}


.filters {
  display: flex;
  align-items: flex-start;
  font-size: 10px;
  font-weight: 500;

  #team {
    margin-right: 15px;
  }

  &-mobile {
    display: none;
    margin-left: auto;
  }

  &__inner {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 100%;
    flex-direction: row;
    flex: 1;
  }

  .filter {
    height: 16px;
    margin: 6px 25px 6px 0;
    display: flex;
    align-items: center;
    color: $clr-txt-topbar;
    transition: all 0.2s;

    &:hover {
      color: $clr-primary;
      cursor: pointer;
    }

    svg {
      margin-right: 7px;
    }
  }
}

@media (max-width: $media-desktop) {
  .filter {
    margin-right: 10px !important;
  }

  .card {
    max-height: 238px;
    max-width: 214px;
  }

  .cards {
    .el-col {
      padding: 0 15px !important;
    }
  }
}

@media (max-width: $media-mobile) {
  .filters {
    align-items: center;

    &__inner {
      display: none;
    }

    &-mobile {
      display: block;
    }
  }


  .card {
    max-width: 273px;
    max-height: 303px;
  }
}

</style>
