<template>
  <el-dialog
      class="new-campaign-modal"
      :before-close="handleClose"
      :visible.sync="value"
      :append-to-body="true">
    <el-container>
      <span class="title">Create a new campaign</span>
    </el-container>
    <el-input type="text" placeholder="Enter new campaign name..."/>
    <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="handleCreate">Create</el-button>
    <el-button @click="handleClose">Close</el-button>
  </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    value: {type: Boolean, required: true,}
  },
  methods: {
    handleClose() {
      this.$emit('input', false);
    },
    handleCreate() {
      if (this.$route.name !== 'Campaign') {
        this.$router.push('/campaign');
        this.handleClose();
      }
    }
  }
};
</script>

<style lang="scss">
.new-campaign-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .el-dialog {
    background-color: $clr-bg-light;
    border-radius: 15px;
    margin: auto 30px;
    width: 100%;
    margin-top: auto !important;

    @media (max-width: $media-tablet) {
      width: 500px;
    }

    @media (max-width: $media-desktop) {
      width: 650px;
    }

    .el-input__inner {
      border-radius: 10px !important;
      border-color: transparent !important;
    }

    &__title {
      display: flex;
    }

    &__body {
      padding-bottom: 25px;

      .title {
        font-size: 24px;
        margin: auto auto 35px;
      }
    }

    &__footer {
      display: flex;
      justify-content: center;
    }
  }
}
</style>

<style lang="scss" scoped>

</style>
